export enum DdzTypeDefine{
    DDZ_SINGLE     = 1,     // 单牌
	DDZ_PAIR       = 2,     // 对子
	DDZ_THREE_ZERO = 3,     // 三条
	DDZ_THREE_ONE  = 4,     // 三带一
	DDZ_THREE_TWO  = 5,     // 三带二
	DDZ_STRAIGHT   = 6,     // 顺子:3,4,5,6,7...A,至少5张
	DDZ_LINK_PAIR  = 7,     // 连对<至少6张:33,44,55...AA>
	DDZ_PLANE_ZERO = 8,     // 飞机<至少6张:333,444...AAA>
	DDZ_PLANE_ONE  = 9,     // 飞机带单翅膀<至少8张:有几个三连就有几个单>
	DDZ_PLANE_TWO  = 10,    // 飞机带双翅膀<至少10张:有几个三连就有几对>
	DDZ_FOUR_ONE   = 11,    // 四带两单
	DDZ_FOUR_TWO   = 12,    // 四带两对
	DDZ_BOMB       = 13,    // 炸弹
	DDZ_ROCKET     = 14,    // 火箭
}

export enum DdzMode{
    Normal = 0,
    Quick
}

